section.contactus {
    background-color: $white3;
    background-image: url('./../../assets/map.png');
    background-repeat: no-repeat;
    height: 1124px;
}

.location {
    height: 174px;
    margin: auto;
    padding-top: 82px;
    position: relative;
    max-width: 345px;

    > img {
        cursor: pointer;
        left: 157px;
        position: absolute;
        top: 186px;
    }
}

.fields {
    background-color: $white3;
    border-radius: 6px;
    box-shadow: 0px 17px 50px rgba(224, 224, 228, 0.656605);
    color: $blue1;
    height: 88px;
    margin-bottom: 15px;
    position: relative;
    max-width: 345px;

    span {
        display: block;
    }

    span:first-of-type {
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 0.2px;
        margin: 16px 0 4px 0;
        text-transform: uppercase;
        width: 250px;
    }

    span:last-of-type {
        font-size: 14px;
        letter-spacing: 0.2px;
    }
}

.fields-cont {
    display: flex;
    height: 100%;

    > div:first-child {
        align-items: center;
        display: flex;
        height: 100%;
        justify-content: center;
        width: 37%;
    }
}

.triangulo {
    border-color: $white3 transparent transparent transparent;
    border-style: solid;
    border-width: 13px 7px 0 7px;
    left: 165px;
    height: 0;
    position: absolute;
    top: 88px;
    width: 0;
}

.popuph:hover {
    transform: scale(1.1);
}

.popuph:active {
    transform: scale(0.8);
}


/* Formulario Contact Us */

#contact {
    background: $white;
    border: 1px solid rgba(198, 201, 210, 0.387183);
    border-radius: 6px;
    box-shadow: 0px 4px 30px $white2;
    height: 738px;
    margin: auto;
    max-width: 701px;

    button {
        height: 50px;
        min-width: 100%;
        max-width: 582px;
    }
}

#contact > div {
    margin: auto;
    max-width: 582px;
}

#contact > div > span {
    color: $blue1;
    display: block;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
}

#contact h2 {
    padding: 44px 0 10px 0;
    text-align: center;
    text-transform: capitalize;
}

#contact input[type="text"], #contact input[type="tel"] {
    color: #82858E;
    border: none;
    font-size: 16px;
    height: 28px;
    line-height: 28px;
    letter-spacing: 0.494018px;
    margin: 36px 0 10px 0;
    opacity: 0.8;
    width: 100%;

    &:focus {
        outline: none;
    }
}

#contact hr {
    margin: 1px 0 1px 0;
    opacity: 0.3;
}

#contact form span {
    color: $blue1;
    display: block;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.494018px;
    padding: 32px 0 21px 0;
}

#contact input[type="radio"] {
    font-size: 16px;
    height: 26px;
    line-height: 28px;
    margin-right: 13px;
    opacity: 0.4;
    width: 26px;
}

#contact label {
    color: #313E5A;
    display: inline-block;
    font-size: 16px;
    letter-spacing: 0.494018px;
    line-height: 28px;
    padding-bottom: 20px;
    vertical-align: middle;
}

#contact label:first-of-type {
    padding-right: 43px;
}

#contact input[name="message"] {
    margin-top: 20px !important;
}

#contact hr:last-of-type {
    margin-bottom: 83px;
}


/* Animacion botÃ³n popup */

.popup {
    -webkit-animation: popups 2.5s linear infinite forwards;
            animation: popups 2.5s linear infinite forwards;
}

@-webkit-keyframes popups {
    0% {
        transform: scale(0.5);
        opacity: 1;
    }
    
    40% {
        transform: scale(2);
        opacity: 0;
    }
    
    100% {
        transform: scale(2);
        opacity: 0;
    }
}

@keyframes popups {
    0% {
        transform: scale(0.5);
        opacity: 1;
    }
    
    40% {
        transform: scale(2);
        opacity: 0;
    }
    
    100% {
        transform: scale(2);
        opacity: 0;
    }
}


/* Estilos dispositivos Res: 320px - 768px */

@media only screen and (min-width: 321px) and (max-width: 768px) {

    section.contactus {
        background-position: center top;
        background-size: 1440px 806px;
    }

    #contact {
        max-width: 643px;
    }

}


/* Estilos dispositivos Res: MAx 320px */

@media only screen and (max-width: 320px) {
    
    section.contactus {
        width: 100%;
        height: 756px;
        background-image: none;
        background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(250,249,252,1) 100%);
        padding-top: 0;
    }

    .location {
        display: none;
    }
 
    #contact {
        width: 300px;
        height: 686px;
    }

    #contact > div {
        width: 260px;
        margin: auto;
    }

    #contact h2 {
        font-size: 28px;
        line-height: 33px;
        padding: 44px 0 7px 0;
    }
    
    #contact > div > span {
        font-size: 14px;
    }
    
    #contact hr {
        opacity: 0.1;
    }
    
    #contact form span {
        font-size: 15px;
    }
    
    #contact input[name="message"] {
        margin-top: 10px !important;
    }
    
    #contact hr:last-of-type {
        margin-bottom: 50px;
    }
    
    #contact button {
        width: 260px;
        height: 40px;
        font-size: 16px;
        line-height: 19px;
    }

}